  --color-icon: var(--dashdock-tab-text-color);
  --color-tabset-background: var(--dashdock-bg-color);
  --color-tabset-background-selected: var(--dashdock-tab-active-bg-color);
  --color-tabset-foreground: var(--dashdock-tab-text-color);
  --color-tabset-foreground-selected: var(--dashdock-tab-active-text-color);
  --border-radius-tabset-tab: var(--mantine-radius-sm, 0.25rem);
  --border-radius-tabset: var(--mantine-radius-md, 0.5rem);
  --border-radius-tab-contents: var(--mantine-radius-sm, 0.25rem);
}

/* Tab styling */
.dash-dock-container .flexlayout__tab {
  background-color: var(--dashdock-tab-bg-color);
  color: var(--dashdock-tab-text-color);
  border-radius: var(--mantine-radius-sm, 0.25rem);
